import Vue from "vue"
import Vuex from "vuex"

Vue.use(Vuex)

export default new Vuex.Store({
  state:{
    order:{
      "counter":1,
      "downmenu":1,
      "radios":1
    },
    totalPrcie:0
  },
  mutations:{
    updataOrder(state,data){

      state.order[data[0]] = data[1];
    },
    updataPrice(state,price){
      state.totalPrcie = price;
    }
  },
  actions:{
    updataOrder(context,data){
      context.commit("updataOrder",data);
    },
    updataPrice(context,price){
      context.commit("updataPrice",price);
    }
  },
  getters:{
    getOrder(state){
      return state.order ? state.order :{}
    },
    getTotalPrice(state){
      return state.totalPrcie >0 ? '￥'+state.totalPrcie : 0;
    }
  }
})



