<template>
    <div class="chooser-component">
        <ul class="chooser-list">
          <li v-for="(item,index) in Radios" :class="{'active':index == nowIndex}" @click="choose(index)">
            {{item.title}}
          </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "Radios",
        data(){
          return{
            nowIndex:0
          }
        },
        methods:{
          choose(index){
            this.nowIndex = index;
            this.$emit("radios",this.Radios[index].value);
            this.$store.dispatch("updataOrder",["radios",this.Radios[index].value])
          }
        },
        props:{
          Radios:{
            type:Array,
            default:function () {
              return [
                {
                  title:"test",
                  value:1
                }
              ]
            }
          }
        }
    }
</script>

<style scoped>
  .chooser-component {
    position: relative;
    display: inline-block;
  }
  .chooser-list li{
    display: inline-block;
    border: 1px solid #e3e3e3;
    height: 25px;
    line-height: 25px;
    padding: 0 8px;
    margin-right: 5px;
    border-radius: 3px;
    text-align: center;
    cursor: pointer;
  }
  .chooser-list li.active {
    border-color: #4fc08d;
    background: #4fc08d;
    color: #fff;
  }
</style>



