import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/pages/Layout'
import Details from '@/pages/Details'

import Hill from  "@/pages/details/hill"
import Car from '@/pages/details/car'
import Earth from '@/pages/details/earth'
import Loud from '@/pages/details/loud'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Layout',
      component: Layout
    },
    {
      path:'/details',
      name:'details',
      component:Details,
      redirect:"/details/hill",
      children:[
        {
          name:"car",
          path:"car",
          component:Car
        },
        {
          name:"hill",
          path:"hill",
          component:Hill
        },
        {
          name:"earth",
          path:"earth",
          component:Earth
        },
        {
          name:"loud",
          path:"loud",
          component:Loud
        }
      ]
    }
  ]
})



