<template>
  <div>
    购物车信息:{{getOrder}}
    <hr>
    总价为：{{getTotalPrice}}
    <button class="button" @click="message">去付款</button>
  </div>
</template>

<script>
    export default {
        name: "earth",
        computed:{
          getOrder(){
            return this.$store.getters.getOrder;
          },
          getTotalPrice(){
            return this.$store.getters.getTotalPrice;
          }
        },
        methods:{
          message(){
            alert("购物愉快");
            this.$router.push("/");
          }
        }
    }
</script>

<style scoped>
  .button{
    background: #4fc08d;
    color: #fff;
    display: inline-block;
    padding: 10px 20px;
    cursor: pointer;
    outline: none;
  }
</style>



